/* The container */
.createUser_container__3-pky {
  display: block;
  position: relative;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.createUser_container__3-pky input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.createUser_checkmark__3Jc6V {
  position: absolute;
  top: 0;
  left: 150px;
  
  height: 25px;
  width: 25px;
  background-color: #ccc;
}

/* On mouse-over, add a grey background color */
.createUser_container__3-pky:hover input ~ .createUser_checkmark__3Jc6V {
  background-color: rgb(188, 249, 188)
}

/* When the checkbox is checked, add a blue background */
.createUser_container__3-pky input:checked ~ .createUser_checkmark__3Jc6V {
  background-color: green;
}

/* Create the checkmark/indicator (hidden when not checked) */
.createUser_checkmark__3Jc6V:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.createUser_container__3-pky input:checked ~ .createUser_checkmark__3Jc6V:after {
  display: block;
}

/* Style the checkmark/indicator */
.createUser_container__3-pky .createUser_checkmark__3Jc6V:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
